    <div class="containerForPhoto" v-if="imageUrl">
      <svg class="delete-button cursor" v-if="imageUrl" @click="deleteImage" xmlns="http://www.w3.org/2000/svg" width="13" height="13" viewBox="0 0 13 13" fill="none">
        <path d="M10.8333 2.70801C10.977 2.70801 11.1148 2.76508 11.2164 2.86666C11.3179 2.96824 11.375 3.10602 11.375 3.24967C11.375 3.39333 11.3179 3.53111 11.2164 3.63269C11.1148 3.73427 10.977 3.79134 10.8333 3.79134H10.2917L10.29 3.8298L9.78467 10.9099C9.76521 11.1832 9.64291 11.439 9.4424 11.6258C9.24188 11.8125 8.97805 11.9163 8.70404 11.9163H4.29542C4.02141 11.9163 3.75758 11.8125 3.55706 11.6258C3.35655 11.439 3.23425 11.1832 3.21479 10.9099L2.70942 3.83034C2.7086 3.81736 2.70823 3.80435 2.70833 3.79134H2.16667C2.02301 3.79134 1.88523 3.73427 1.78365 3.63269C1.68207 3.53111 1.625 3.39333 1.625 3.24967C1.625 3.10602 1.68207 2.96824 1.78365 2.86666C1.88523 2.76508 2.02301 2.70801 2.16667 2.70801H10.8333ZM7.58333 1.08301C7.72699 1.08301 7.86477 1.14008 7.96635 1.24166C8.06793 1.34324 8.125 1.48102 8.125 1.62467C8.125 1.76833 8.06793 1.90611 7.96635 2.00769C7.86477 2.10927 7.72699 2.16634 7.58333 2.16634H5.41667C5.27301 2.16634 5.13523 2.10927 5.03365 2.00769C4.93207 1.90611 4.875 1.76833 4.875 1.62467C4.875 1.48102 4.93207 1.34324 5.03365 1.24166C5.13523 1.14008 5.27301 1.08301 5.41667 1.08301H7.58333Z" fill="#F24949"/>
      </svg>
      <img class="uploadDocs_uploadPhotoCustomPhoto" :src="imageUrl" alt="Завантажене зображення" v-if="imageUrl">
    </div>
    <div class="uploadDocs_uploadPhoto" v-if="!imageUrl">
      <div class="custom-file-input">
        <label for="fileInput" class="custom-label">
          <svg xmlns="http://www.w3.org/2000/svg" width="22" height="20" viewBox="0 0 22 20" fill="none">
            <path d="M18 6V4H16V2H18V0H20V2H22V4H20V6H18ZM2 20C1.45 20 0.979333 19.8043 0.588 19.413C0.196 19.021 0 18.55 0 18V6C0 5.45 0.196 4.97933 0.588 4.588C0.979333 4.196 1.45 4 2 4H5.15L7 2H13V4H7.875L6.05 6H2V18H18V9H20V18C20 18.55 19.8043 19.021 19.413 19.413C19.021 19.8043 18.55 20 18 20H2ZM10 16.5C11.25 16.5 12.3127 16.0627 13.188 15.188C14.0627 14.3127 14.5 13.25 14.5 12C14.5 10.75 14.0627 9.68733 13.188 8.812C12.3127 7.93733 11.25 7.5 10 7.5C8.75 7.5 7.68733 7.93733 6.812 8.812C5.93733 9.68733 5.5 10.75 5.5 12C5.5 13.25 5.93733 14.3127 6.812 15.188C7.68733 16.0627 8.75 16.5 10 16.5ZM10 14.5C9.3 14.5 8.70833 14.2583 8.225 13.775C7.74167 13.2917 7.5 12.7 7.5 12C7.5 11.3 7.74167 10.7083 8.225 10.225C8.70833 9.74167 9.3 9.5 10 9.5C10.7 9.5 11.2917 9.74167 11.775 10.225C12.2583 10.7083 12.5 11.3 12.5 12C12.5 12.7 12.2583 13.2917 11.775 13.775C11.2917 14.2583 10.7 14.5 10 14.5Z" fill="#F24949"/>
          </svg>
          <input
              type="file"
              id="fileInput"
              ref="fileInput"
              class="custom-input"
              @change="uploadImage"
              accept="image/*"
          />
        </label>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    initialValue: String,
    index: Number, // Додано пропс для передачі індексу зображення
  },
  data() {
    return {
      imageUrl: this.initialValue || null,
    };
  },
  methods: {
    uploadImage() {
      // Оновлено код, щоб передавати індекс зображення разом з URL
      this.imageUrl = null;
      const fileInput = this.$refs.fileInput;
      if (fileInput.files.length > 0) {
        const selectedFile = fileInput.files[0];
        const reader = new FileReader();

        reader.onload = () => {
          this.imageUrl = reader.result;
          this.$emit('imageUrlSend', this.imageUrl, this.index); // Оновлено подію
        };

        reader.readAsDataURL(selectedFile);
      }
    },
    deleteImage() {
      this.imageUrl = null;
      this.$emit('imageUrlSend', this.imageUrl, this.index); // Оновлено подію
    },
  },
};
</script>

