<template>
  <div class="custom-select">
    <div class="selected-item" @click="toggleDropdown">{{ selectedName }} <div :class="isDropdownOpen ? 'select_imgRotate rotate' : 'select_imgRotate'">
      <svg xmlns="http://www.w3.org/2000/svg" width="11" height="6" viewBox="0 0 11 6" fill="none">
        <path d="M0.478478 0.312951C0.655989 0.135439 0.933765 0.119302 1.12951 0.264538L1.18558 0.312951L5.4987 4.62584L9.81181 0.312951C9.98932 0.135439 10.2671 0.119302 10.4628 0.264538L10.5189 0.312951C10.6964 0.490462 10.7126 0.768237 10.5673 0.963978L10.5189 1.02006L5.85225 5.68672C5.67474 5.86423 5.39696 5.88037 5.20122 5.73514L5.14514 5.68672L0.478478 1.02006C0.283216 0.824795 0.283216 0.508213 0.478478 0.312951Z" fill="#181818"/>
      </svg>
    </div></div>
    <ul class="dropdown" v-show="isDropdownOpen">
      <li v-for="option in options" :key="option.id" @click="selectOption(option)">
        <img :src="option.image" alt="" class="option-image" />
        {{ option.name }}
        ID {{option.id}}
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  props:{
    options:{
      required: true
    }
  },
  data() {
    return {
      isDropdownOpen: false,
      selectedOption: null,
    };
  },
  computed: {
    selectedName() {
      return this.selectedOption ? this.selectedOption.name : '--';
    },
  },
  methods: {
    toggleDropdown() {
      this.isDropdownOpen = !this.isDropdownOpen;
    },
    selectOption(option) {
      this.$emit('selectOptionIdAndName', option.id,option.name)
      this.selectedOption = option;
      this.isDropdownOpen = false;
    },
  },
};
</script>

<style scoped>
/* Стилізуйте ваш кастомний селект за вашими потребами */
.custom-select {
  position: relative;
  width: 100%;
}

.selected-item {
  cursor: pointer;
  display: flex;
  height: 54px;
  padding: 15px 13px;
  justify-content: space-between;
  align-items: center;
  align-self: stretch;
  border-radius: 10px;
  border: 4px solid var(--black-black-400-ededed, #EDEDED);
}

.dropdown {
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  list-style: none;
  padding: 0;
  margin: 0;
  background-color: #fff;
}

.dropdown.show {
  display: block;
}

.dropdown li {
  cursor: pointer;
  display: flex;
  height: 54px;
  padding: 15px 13px;
  justify-content: space-between;
  align-items: center;
  align-self: stretch;
  border-radius: 10px;
  margin-top: 5px;
  border: 4px solid var(--black-black-400-ededed, #EDEDED);
}

.option-image {
  width: 30px;
  height: 30px;
  margin-right: 10px;
}
.rotate{
  transition: .3s all;
  transform: rotate(180deg);
}
</style>
