<script>
import CustomInput from "@/UI/CustomInput.vue";
import eyeOnIcon from '../icons/eye.svg';
import eyeOffIcon from '../icons/eye-off.svg';
export default {
  components: {CustomInput},
  data() {
    return {
      inputPasswordText: "",
      inputNameText: "",
      inputRepeatPassword: "",
      showPassword: false,
      showPasswordRepeatPassword: false,
      inputEmailText: "",
      checked: false,
      eyeOnIcon,
      eyeOffIcon,
      modal: false,

    };
  },
  methods: {
    modalHandler() {
      const rootElement = document.documentElement;

      if(this.modal) {
        this.modal = false
        rootElement.classList.remove("no-scroll");
      } else {
        this.modal = true
        rootElement.classList.add("no-scroll");
      }
    },
    togglePasswordVisibility() {
      this.showPassword = !this.showPassword;
    },
    togglePasswordRepeatVisibility() {
      this.showPasswordRepeatPassword = !this.showPasswordRepeatPassword;
    },
    submitForm(e) {
      if(this.inputRepeatPassword === this.inputPasswordText){
        console.log('ok')
      }else(
          console.log('not ok')
      )
      e.preventDefault()
      this.inputPasswordText = "";
      this.inputNameText = "";
      this.inputRepeatPassword = "";
      this.showPassword = "";
      this.showPasswordRepeatPassword = "";
      this.inputEmailText = "";
      this.checked = false;
    },
  }
}
</script>

