<template>
  <customSelect
      v-bind:options="options"
      v-bind:selected="selected"
      v-bind:selectedTag="selectedTag"
      v-bind:classCustomSelect="classCustomSelect"
      v-bind:checkedTopOrBottom="checkedTopOrBottom"
  />
    <greenFastCategoryButton
        v-for="category in categories"
        :key="category"
        :category="category"
        :activeButton="activeButton"
        @button-click="handleCategoryButtonClick"
    ></greenFastCategoryButton>
<!--    <greenFastCategoryButton-->
<!--        @click="handleButtonClick('Discount')"-->
<!--        :class="{'activeBtn': activeButton === 'Discount'}"-->
<!--    >Discount</greenFastCategoryButton>-->
<!--    <greenFastCategoryButton-->
<!--        @click="handleButtonClick('Hot girl')"-->
<!--        :class="{'activeBtn': activeButton === 'Hot girl'}"-->
<!--    >Hot girl</greenFastCategoryButton>-->
<!--    <greenFastCategoryButton-->
<!--        @click="handleButtonClick('Girl of the month')"-->
<!--        :class="{'activeBtn': activeButton === 'Girl of the month'}"-->
<!--    >Girl of the month</greenFastCategoryButton>-->
</template>

<script>
import greenFastCategoryButton from "@/UI/greenFastCategoryButton.vue";
import CustomSelect from "@/components/customSelect.vue";

export default {
  components: {CustomSelect, greenFastCategoryButton},

  data(){
    return{
      options: [{name: "Girls", tag:"Vip"}, {name: "Transgenders", tag: "Vip"}, {name: "Gais", tag: "Vip"}],
      selected: 'Vip', // Початково вибрано "Fr"
      selectedTag: 'Vip ', // Назва яка над кастом селект
      classCustomSelect: 'transparent',
      checkedTopOrBottom: false,
      categories: ['TOP 100', 'Discount', 'Hot girl', 'Girl of the month'],
      activeButton: '', // Початкова активна категорія
    }
  },

  methods: {
    handleButtonClick(buttonName) {
      if(this.activeButton == buttonName){
        this.activeButton = 0
      }else{
        this.activeButton = buttonName;
      }
    },
    handleCategoryButtonClick(category) {
      if(this.activeButton === category){
        this.activeButton = 0
      }else{
        this.activeButton = category;
      }
    },
  },
}
</script>

<style lang="scss">
.activeBtn{
  background: #F24949;
}
</style>
