<template>
  <div class="input_select">
    <p class="specialTextTop_text" v-if="specialTextTop">{{ specialTextTop }}</p>
    <div :class="[classCustomSelect || 'custom-select', { 'activeBack': showDropdown }]">
      <div :class='selectedContainer' @click="toggleDropdown">
        <span :class='selectedOption'>{{ selectedTag }}</span>
        <img src="../icons/Down.svg" alt="" :class="{' activeToogle ':showDropdown}" v-if="!anotherArrowDown">
        <img src="../icons/DownBlack.svg" alt="" :class="{' activeToogle ':showDropdown}" v-else>
      </div>
      <ul style="z-index: 100000" :class="dropdownOptionsAddedClass" class="dropdown-options" v-show="showDropdown" v-if="checkedTopOrBottom">
        <li style="width: 100%" v-for="option in filteredOptions" @click="selectOption(option)" :key="option.name" :class="{'activeName': selectedTag === option.tag, 'inactive': selectedTag !== option.tag}">{{ option.name }}</li>
      </ul>
      <ul style="z-index: 100000" class="dropdown-options-second" v-show="showDropdown" v-else>
        <li style="width: 100%" v-for="option in filteredOptions" @click="selectOption(option)" :key="option.name" :class="{'active_class': option.name === selected}">{{ option.name }}</li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    options: {
      type: Array,
      required: true
    },
    selectedTag:{
      type: String,
      required: true
    },
    selected: {
      type: String,
      required: true
    },
    classCustomSelect: {
      type: String,
      required: false
    },
    checkedTopOrBottom: {
      type: Boolean,
      required: true
    },
    anotherArrowDown:{
      type: Boolean,
      required:false,
      default: false
    },
    selectedContainer:{
      type: String,
      required: false,
      default: 'selected-container'
    },
    dropdownOptionsAddedClass:{
      type: String,
      required: false
    },
    specialTextTop:{
      type: String,
      required: false
    },
    selectedOption:{
      type: String,
      required:false,
      default: 'selected-option'
    }
  },

  data() {
    return {
      showDropdown: false,
      selected: this.selected,
      selectedTag: this.selectedTag,
      classCustomSelect: this.classCustomSelect,
      checkedTopOrBottom: this.checkedTopOrBottom,
      anotherArrowDown: this.anotherArrowDown,
      dropdownOptionsAddedClass: this.dropdownOptionsAddedClass
    };
  },
  computed: {
    filteredOptions() {
      return this.options.filter(option => option !== this.selected);
    },
  },
  methods: {
    toggleDropdown() {
      this.showDropdown = !this.showDropdown;
    },
    selectOption(option) {
      this.$emit('changeActiveTagName', option.name)
      this.selected = option.name;
      this.selectedTag = option.tag;
      this.showDropdown = false;
    },
  },
};
</script>

<style lang="scss">
.specialTextTop_text{
  color: var(--black-black-900181818, #181818);
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 16px;
  text-align: left;
  margin-bottom: 9px;
}
.input_select{
  cursor: pointer;
  position: relative;
}

.active_class{
  color: var(--primary-primary-800-f-24949, #F24949)
}
.transparent{
  cursor: pointer;
  position: relative;
  padding: 10px;
  width: 62px;
  gap: 3px;
  border-radius: 11px;
  background: var(--success-success-80052-a-436, #52A436);
}
.transparent.activeBack{
    background: var(--primary-primary-800-f-24949, #F24949);
}
.dropdown-options{
  position: absolute;
  right: 50%;
  transform: translateX(50%);

  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 16px; /* 114.286% */

  display: flex;
  padding: 8px 13px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 10px;

  border-radius: 0px 0px 8px 8px;
  background: #FFF;
  box-shadow: 0px 4px 28px 0px rgba(0, 0, 0, 0.10);

  .activeName{
    color: rgb(140, 140, 140);
  }
  .inactive{
    color: var(--black-black-900181818, #181818);
  }
}
.dropdown-options-second{
  position: absolute;
  top: 100%;
  left: -30%;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 16px; /* 114.286% */

  display: flex;
  padding: 8px 13px;
  flex-direction: column;
  justify-content: center;
  gap: 10px;

  border-radius: 0px 0px 8px 8px;

  .active{
    color: rgb(140, 140, 140);
  }
  .inactive{
    color: var(--black-black-900181818, #181818);
  }
  li{
    padding: 6px 104px 5px 9px;
    border-radius: 5px;
    background: #ffffff;
    text-align: left;
    cursor: pointer;
  }
}
.custom-select{
  cursor: pointer;
  position: relative;
}
.selected-option{
  color: #FFF;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 16px; /* 114.286% */
}
.selected-container{
  display: flex;
  align-items: center;
}
.activeToogle{
  transform: rotate(180deg);
}
</style>
<style lang="scss" scoped>
li{
  cursor: pointer;
}
</style>