<template>
 <div style="position: relative" class="input_box" :class="{reverse_box : reverse}">
   <img src="../icons/Search.svg" alt="" class="img-icon reve" v-if="isImg && reverse" >

   <input  :name="inputName" :class='inputClass' :type="inputType" :placeholder='placeholder'  :value="modelValue" @input="updateInput" v-if="!isInput">

   <select  class="custom_input_select" v-if="isInput" :name="inputName" :class="inputClass" :value="modelValue" @input="updateInput">
     <option v-for="option in optionsSelect" :value="option.value">{{ option }}</option>
   </select>
   <img src="../icons/Search.svg" alt="" class="img-icon" v-if="isImg && !reverse">
 </div>
</template>

<script>
export default {
  props: {
    reverse: false,
    placeholder: {
      type: String,
      required: true
    },
    inputClass: {
      type: String,
      required: true,
    },
    inputName: {
      type: String,
      required: false,
    },
    inputType: {
      type: String,
      required: false,
      default: 'text'
    },
    isImg:{
      type: Boolean,
      required: false,
      default: true
    },
    optionsSelect: {
      type: Array,
      required: false,
      default: () => []
    },
    isInput: false,
    modelValue: [String, Number]
  },
  data() {
    return {
      inputClass : this.inputClass
    }
  },
  methods: {
    updateInput(event) {
      this.$emit('update:modelValue', event.target.value)
    }
  }
}

</script>

<style lang="scss" scoped>
.img-icon{
  position: absolute;
  right: 10px;
  top: 50%;
  transform: translateY(-50%);
  cursor: pointer;
}
.reve {
  right: 225px!important;
  //margin-left: 10px;
}
.reverse_box {
  display: flex;
  //flex-direction: row-reverse;
}
</style>
<style lang="scss">
.loginModal_Password {
  gap: 10px;
  display: flex;
  flex-direction: column;
}
</style>