      },
      activeStep: 'step1',
      selectedCountry: '',
      model: '12',
      note: false
    }
  },
  mounted() {
    for (let i = 0; i <= 60; i++) {
      const minute = i < 10 ? `0${i}` : `${i}`;
      this.optionsMinute.push(minute);
