<script>
import CustomInput from "@/UI/CustomInput.vue";
import eyeOnIcon from '../icons/eye.svg';
import eyeOffIcon from '../icons/eye-off.svg';
import axios from "axios";
export default {
  components: {CustomInput},
  data() {
    return {
      inputPasswordText: "",
      inputNameText: "",
      inputRepeatPassword: "",
      showPassword: false,
      showPasswordRepeatPassword: false,
      inputEmailText: "",
      checked: false,
      eyeOnIcon,
      eyeOffIcon,
      modal: false,

    };
  },
  methods: {
    modalHandler() {
      const rootElement = document.documentElement;

      if(this.modal) {
        this.modal = false
        rootElement.classList.remove("no-scroll");
      } else {
        this.modal = true
        rootElement.classList.add("no-scroll");
      }
    },
    togglePasswordVisibility() {
      this.showPassword = !this.showPassword;
    },
    togglePasswordRepeatVisibility() {
      this.showPasswordRepeatPassword = !this.showPasswordRepeatPassword;
    },
    submitForm(e) {

      e.preventDefault();

      const formData = {
        nom_email: this.inputEmailText.valueOf(),
        nom_name: this.inputNameText.valueOf(),
        nom_lastName: this.inputNameText.valueOf(),
        nom_password: this.inputPasswordText.valueOf(),
      };

      axios({
        method: 'post',
        url: 'http://149.102.144.124/?registration',
        data: {
          nom_email: JSON.stringify(this.inputEmailText),
          nom_name: JSON.stringify(this.inputNameText),
          nom_lastName:  JSON.stringify(this.inputNameText),
          nom_password: JSON.stringify(this.inputPasswordText),
        }
      }).then(function (response) {
        console.log(response)
        console.log(JSON.stringify(formData))
        // Обработайте успешный ответ здесь
        console.log('Успешный ответ:', response.data);
      })
          .catch(function (error) {
            // Обработайте ошибку здесь
            console.error('Ошибка:', error);
          })

      // this.inputPasswordText = '';
      // this.inputNameText = '';
      // this.inputRepeatPassword = '';
      // this.inputEmailText = '';
      // this.checked = false;
    }
  }
}
</script>

