<template>
  <!--  <hr/>-->
  <div class="checkFact_container choose_container choose_off">
    <div class="checkFact_container_btn">
      <div class="choose_title">Choisissez une <span>ville</span>: </div>
      <button-select @click="showContentHandler" :title="title" :cities="cities" />
    </div>
    <div class="checkFast_searchBox">
      <customInput v-bind:placeholder="placeholder" v-bind:inputClass="inputClass" v-model="nameFromInput"/>
    </div>
  </div>
  <div class="filter_box">
    <button @click="handleContentOn" class="btn_box">
      City
      <img src="../../icons/filter.svg" alt="">
    </button>

    <div class="grid choose_content none_choose_content" v-if="showContent">
      <div class="row" v-for="row in 2" :key="row">
        <div class="col" v-for="column in 4" :key="column">
          <div class="cell">
            <span :class="{ active_country: isSelected(city) }" @click="toggleCity(city)" v-for="(city, index) in cities[row * 4 - 4 + column - 1]" :key="index">{{ city }}</span>
          </div>
        </div>
      </div>
    </div>

  </div>
</template>

<script>
import ButtonSelect from "@/components/buttonSelect.vue";
import CustomInput from "@/UI/CustomInput.vue";

export default {
  components: {CustomInput, ButtonSelect},
  data(){
    return{
      title: 'All',
      placeholder: "Rechercher votre ville:",
      inputClass: "inputSearch",
      nameFromInput: '',
      cities: [
        ["Aix-En-Provence (35)", "Annecy (8)", "Avignon (12)", "Bastia (8)", "Bordeaux (26)", "Cannes (24)"],
        ["Dijon (6)", "Grenoble (11)", "Lille (18)", "Lyon (40)", "Marseille (30)", "Monaco (24)"],
        ["Montpellier (9)", "Nancy (7)", "Nantes (25)", "Nice (28)", "Orléans (7)", "Paris (41)"],
        ["Reims (12)", "Rennes (7)", "Strasbourg (28)", "Toulon (5)", "Toulouse (34)", "Tours (7)"],
        ["Adge (2)", "Ajaccio (8)", "Amiens (12)", "Angers (8)", "Belfort (26)", "Besancon (24)"],
        ["Biarritz (6)", "Chambery (11)", "Charleville-Mezieres (18)", "Colmar (40)", "Geneve (30)", "LeMans (24)"],
        ["Adge(2)", "Ajaccio(8)", "Amiens(12)", "Angers(8)", "Belfort(26)", "Besancon(24)"],
        ["Biarritz(6)", "Chambery(11)", "Charleville-Mezieres(18)", "Colmar(40)", "Geneve(30)", "LeMans(24)"]
      ],
      showContent: false,
      activeCityIndex: -1,
      // selectedCity: [[], [], [], [], [], [], [], [], [], [], [], [], [], [], [], [], [], [], [], [], [], [], [], [], [], [], [], [], [], [], [], [], [], [], [], [], [], [], [], [], [], [], [], [], [], [], [],],
      selectedCities: [],
    };
  },
  methods: {
    showContentHandler() {
      this.showContent = !this.showContent;
    },
    handleContentOn() {
      this.$store.state.showContent = true
      const rootElement = document.documentElement;
      rootElement.classList.add("no-scroll");

    },
    toggleCity(city) {
      const index = this.selectedCities.indexOf(city);
      if (index === -1) {
        this.selectedCities.push(city);
      } else {
        this.selectedCities.splice(index, 1);
      }
    },
    isSelected(city) {
      return this.selectedCities.includes(city);
    },
  },


}
</script>

<style lang="scss" scoped>
hr {
  width: 100%;
  height: 1px;
  background: #EDEDED;
  position: absolute;
  margin: 0 auto;
}
.active_country {
  text-decoration: underline; /* Додайте стиль для активного міста */
}
.choose_title {
  font-family: Josefin Sans;
  font-size: 36px;
  font-style: normal;
  font-weight: 600;
  line-height: 32px; /* 88.889% */
  span {
    color: #F24949;
    /* H2 */
    font-family: Josefin Sans;
    font-size: 36px;
    font-style: normal;
    font-weight: 600;
    line-height: 32px;
  }
}
.choose_container {
  padding-top: 90px;
}
.cell {
  span {
    display: block;
    padding: 10px 0 10px 0;
  }

}
.grid {
  display: grid;
  //grid-template-columns: repeat(6, 1fr);
  grid-template-rows: repeat(2, 1fr);
  gap: 55px;
}
.text_box {
  color: #000;
  /* H1 mobile */
  font-family: Josefin Sans;
  font-size: 20px;
  font-style: normal;
  font-weight: 700;
  line-height: 14px; /* 70% */
}
.flex_start {
  justify-content: start!important;
  display: flex;
}
.row {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 20px;

}
.col {
  width: 239px;
}
.adaptive_choose_content {
  display: none;
  margin-top: 30px;
}
@media (max-width: 1200px) {
  .adaptive_choose_content {
    display: grid;
  }
  .none_choose_content{
    display: none;
  }
}

</style>