 <template>
  <div id="demo-modal" class="modal">
    <div class="modal__content"  :class="{smallModal : isResetBtn}">
      <div class="loginModal_title">{{inputTitle}}</div>
        <div class="loginModal_login"  v-if="isInput">
          <span class="span_login" >{{ inputText }}</span>
          <customInput :class="{error : error}" class="input_login" v-bind:placeholder="placeholder" v-bind:inputClass="inputClass" v-bind:is-img="false" v-model="usernameFromInput" />
          <span :class="{error : error, errorNone : !error}" >Username must not contain symbols</span>
        </div>
        <div class="loginModal_Password" v-if="isInput1">
          <span class="span_login" >Password</span>
          <customInput v-bind:placeholder="placeholder1" v-bind:inputClass="inputClass" v-bind:is-img="false" v-model="passwordFromInput" />
          <span :class="{wrong : wrong, errorNone : !wrong}" >Wrong login or password</span>
        </div>
        <div class="modal_blocks">
          <div class="header_content_container-btn ">
            <button v-if="isLoginBtn" @click="validateForm" class="btn_white header_content_btn modal_btn">
              Log in
            </button>
            <button v-if="isLoginBtn" class="btn_white header_content_btn ">
              Sign Up
            </button>
            <button v-if="isResetBtn" @click="validateForm" class="btn_white header_content_btn modal_btn">
              Send
            </button>
          </div>
          <div v-if="!isResetBtn" @click="openReset" class="loginModal_reset">
            Mot de passe oublié?
          </div>
        </div>
      <a @click="toggleModal" class="modal__close">&times;</a>
    </div>
  </div>

</template>

<script>

import {defineComponent} from "vue";
import CustomInput from "@/UI/CustomInput.vue";

export default defineComponent({
  components: {CustomInput},
  data() {
    return {
      inputClass: "inputSearch input_login",
      usernameFromInput: '',
      passwordFromInput: '',
      error: false,
      wrong: false,
      onModal: false
    }
  },
  props: {
    inputTitle: '',
    inputText: '',
    placeholder: '',
    isInput: '',
    inputTitle1: '',
    inputText1: '',
    placeholder1: '',
    isInput1: '',
    isLoginBtn: true,
    isResetBtn: false
  },
  methods: {
    validateForm() {
      const specialCharacters = /[!@#$%^&*()_+\-=\[\]{};':"\\|,.<>\/?]+/;
      if (specialCharacters.test(this.usernameFromInput)) {
        this.wrong = true;
        this.error = false;
      } else {
        this.error = true;
      }
    },
    toggleModal() {
      this.$store.state.ifModal = false
      this.$store.state.ifReset = false
      const rootElement = document.documentElement;
      rootElement.classList.remove("no-scroll");
    },
    openReset() {
      this.toggleModal()
      this.$store.state.ifReset = !this.$store.state.ifReset
    },
  },


})
</script>

<style lang="scss" >
.smallModal {
  width: 480px!important;
  height: 263px!important;
}
.errorNone {
  display: none;
}
.wrong {
  color: var(--primary-primary-800-f-24949, #F24949);
  font-family: Josefin Sans;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: 16px; /* 133.333% */
  display: flex!important;
  border-radius: 6px;
  background: var(--primary-primary-400-fecccc, #FECCCC);
  width: 100%;
  height: 26px;
  align-items: center;
  padding-left: 10px;
  margin-top: 10px;
}
.error {
  color: var(--primary-primary-800-f-24949, #F24949);
  font-family: Josefin Sans;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: 16px; /* 133.333% */
  display: inline-block!important;
}
.wrapper {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  /* End center */
  background: -webkit-linear-gradient(to right, #834d9b, #d04ed6);
  background: linear-gradient(to right, #834d9b, #d04ed6);
}

.wrapper a {
  display: inline-block;
  text-decoration: none;
  padding: 15px;
  background-color: #fff;
  border-radius: 3px;
  text-transform: uppercase;
  color: #585858;
  font-family: 'Roboto', sans-serif;
}

.modal {
  //visibility: hidden;
  //opacity: 0;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(77, 77, 77, .7);
  transition: all .4s;
  z-index: 20;
  &_btn {
    border-radius: 30px;
    background: var(--primary-primary-800-f-24949, #F24949);
    color: #FFFFFF;
  }
  &_blocks {
    display: flex;
    justify-content: space-between;
  }
}
.modal:target {
  visibility: visible;
  opacity: 1;
}
.modal__content {
  border-radius: 4px;
  position: relative;
  width: 500px;
  max-width: 90%;
  background: #fff;
  padding: 1em 2em;
  display: flex;
  flex-direction: column;
  width: 450px;
  height: 415px;
  //gap: 20px;
  justify-content: space-evenly;
  padding: 0 31px 0 31px;
  border-radius: 20px;
  height: 415px;
}

.modal__footer {
  text-align: right;
  a {
    color: #585858;
  }
  i {
    color: #d02d2c;
  }
}
.modal__close {
  position: absolute;
  top: 10px;
  right: 10px;
  color: #585858;
  text-decoration: none;
  cursor: pointer;
}
.loginModal {
  width: 100%;
  height: 100vh;
  background: #000000;
  opacity: 0.5%;
  z-index: 9;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  &_block {
    width: 450px;
    height: 415px;
    background: #FFFFFF;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 10;
  }
  &_title {
    color: var(--black-black-900181818, #181818);
    /* H3 */
    font-family: Josefin Sans;
    font-size: 24px;
    font-style: normal;
    font-weight: 600;
    line-height: 24px; /* 100% */
  }
  &_login, &_password {
    font-family: Josefin Sans;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 16px; /* 114.286% */
  }
  &_reset {
    display: flex;
    justify-content: end;
    cursor: pointer;
    align-items: center;
  }
}
.input_login {
  margin-top: 5px;
  width: 100%!important;
}
</style>
 <style lang="scss" scoped>
 .header_content_btn {
   border-radius: 30px;
   border: 1px solid var(--primary-primary-800-f-24949, #F24949);
 }
 .modal_btn {
   border-radius: 30px;
   background: var(--primary-primary-800-f-24949, #F24949);
   color: #FFFFFF;
 }
 </style>