  methods: {
    modalHandler() {
      const rootElement = document.documentElement;

      if(!this.modal) {
        this.modal = true
        rootElement.classList.add("no-scroll");
      } else {
        this.modal = false
        rootElement.classList.remove("no-scroll");

      }
    },
    activeToggle() {
      this.activeCard = !this.activeCard
      console.log(this.activeCard)
    },
    toggleNav(index) {
      this.isActiveNav = index
    },
    goToPage(page) {
      if (page === 'prev' && this.activePage > 1) {
        this.activePage--;
      } else if (page === 'next' && this.activePage < this.totalPage) {
        this.activePage++;
      } else if (typeof page === 'number') {
        this.activePage = page;
      }
    },
  },
  computed: {
    sortedCards() {
      return this.cards.sort((a, b) => (a.premium === b.premium ? 0 : a.premium ? -1 : 1));
