    },
    displayedPages() {
      console.log('%c ЛоЛ ', 'background: white; color: red');
      const pages = [];
      const numPagesToShow = 7; // Adjust this value to your desired number of pages to show

      if (this.totalPage <= numPagesToShow) {
        // If there are fewer pages than the desired number, display all pages
        for (let i = 1; i <= this.totalPage; i++) {
          pages.push(i);
        }
      } else {
        // If there are more pages than the desired number, adjust based on active page
        if (this.activePage <= numPagesToShow - 3) {
          // Display pages 1 to numPagesToShow-2 and add ellipsis
          for (let i = 1; i <= numPagesToShow - 2; i++) {
            pages.push(i);
          }
          pages.push('...');
          pages.push(this.totalPage);
        } else if (this.activePage >= this.totalPage - numPagesToShow + 3) {
          // Display ellipsis and pages totalPage-(numPagesToShow-2) to totalPage
          pages.push(1);
          pages.push('...');
          for (let i = this.totalPage - numPagesToShow + 3; i <= this.totalPage; i++) {
            pages.push(i);
          }
        } else {
          // Display ellipsis and pages activePage-1 to activePage+1
          pages.push(1);
          pages.push('...');
          for (let i = this.activePage - 1; i <= this.activePage + 1; i++) {
            pages.push(i);
          }
          pages.push('...');
          pages.push(this.totalPage);
        }
      }
      return pages;
    },
