    methods: {
      uploadImage() {
        this.imageUrl = null;
        this.mediaUrl = null;
        const fileInput = this.$refs.fileInput;
        if (fileInput.files.length > 0) {
          const selectedFile = fileInput.files[0];
          const reader = new FileReader();

          reader.onload = () => {
            this.imageUrl = reader.result;
          }

          reader.readAsDataURL(selectedFile);
        }
      },
      deleteImage() {
        this.imageUrl = null;
      },
      uploadMedia() {
        this.imageUrl = null;
        this.mediaUrl = null;
        const mediaInput = this.$refs.mediaInput;
        if (mediaInput.files.length > 0) {
          const selectedMedia = mediaInput.files[0];
          if (this.mediaUrl !== URL.createObjectURL(selectedMedia)) {
            this.mediaUrl = URL.createObjectURL(selectedMedia);

            // Перевіряємо, чи існує videoPlayer перед викликом методів
            const videoPlayer = this.$refs.videoPlayer;
            if (videoPlayer) {
              videoPlayer.load();
              videoPlayer.play();
            }
          }
        } else {
          this.mediaUrl = null;
        }
      },


      deleteMedia() {
        this.mediaUrl = null;
      },
    },
