<template>
  <div class="choose-box">
    <button class="choose_btn" @click="showContentHandler">
      <img :class="{ active: showContent }" src="../icons/ArrowDown.svg" alt="стрілка вниз">
      <span class="text">{{ title }}</span>
    </button>
  </div>
</template>

<script>
import CustomInput from "@/UI/CustomInput.vue";

export default {
  components: { CustomInput },
  props: {
    title: {
      type: String,
      required: true,
    },
  },
  data() {
    return {
      showContent: false,
    };
  },
  methods: {
    showContentHandler() {
      this.showContent = !this.showContent;
    },
  },
};
</script>
<style lang="scss">
.choose {
  &_btn {
    display: flex;
    padding: 10px 22px;
    justify-content: center;
    align-items: center;
    gap: 2px;
    border-radius: 30px;
    border: 1px solid var(--primary-primary-800-f-24949, #F24949);
    background: #FFF;
    color: #F24949;
    margin-left: 20px;
  }
  &_content {
    position: relative;
    margin-top: 64px;
  }
}
.active {
  transform: rotate(180deg);
}

</style>