<template>
  <div class="fullscreenModal" :style="{ display: modalView ? 'block' : 'none' }">
    <div class="fullscreenModal_mainPhoto">
      <div>
        <img :src="activePhoto" alt="" class="fullscreenModal_mainPhoto_img">
        <div class="fullscreenModal_info">
          <h2>
            Jesica,
          </h2>
          <img src="../../icons/LocationWhite.svg" alt="">
          <h3>Monaco</h3>
        </div>
      </div>
    </div>
    <div class="fullscreenModal_close cursor" @click="toogleModalWindow(false)">
      <img src="../../icons/Close.svg" alt="image">
    </div>
    <div class="fullscreenModal_bottomContent">
      <img
          v-for="(photo, index) in modalPhotos"
          :key="index"
          :src="photo"
          alt=""
          :class="{
            'activePhoto': activePhotoIndex === index,
            'bottomContentPhoto': activePhotoIndex !== index
          }"
          @click="selectPhoto(photo,index)"
      />

    </div>
  </div>
</template>

<script>

export default {
  props: ['modalView'],
  data(){
    return{
      modalPhotos:[
        'https://source.unsplash.com/random/136x164?1',
        "https://source.unsplash.com/random/136x164?1",
        "https://source.unsplash.com/random/136x164?2",
        "https://source.unsplash.com/random/136x164?3",
        "https://source.unsplash.com/random/136x164?4",
        "https://source.unsplash.com/random/136x164?5",
        "https://source.unsplash.com/random/136x164?6",
        "https://source.unsplash.com/random/136x164?7",
        "https://source.unsplash.com/random/136x164?8",
        "https://source.unsplash.com/random/136x164?9",
        "https://source.unsplash.com/random/136x164?10",
        "https://source.unsplash.com/random/136x164?11",
        "https://source.unsplash.com/random/136x164?12",
        "https://source.unsplash.com/random/136x164?13",
        "https://source.unsplash.com/random/136x164?14",
        "https://source.unsplash.com/random/136x164?15"

      ],
      activePhotoIndex: 0,
    }
  },
  computed: {
    activePhoto() {
      // Використовуємо обчислену властивість для отримання активної фотографії
      return this.modalPhotos[this.activePhotoIndex];
    }
  },
  methods: {
    selectPhoto(photo,index) {
      this.activePhoto = photo;
      this.activePhotoIndex = index;
    },

    toogleModalWindow(data) {
      this.$emit('updateData', data); // Відправляємо подію для зміни даних в батьківському компоненті
    },
  }

}
</script>

<style lang="scss" >
.activePhoto{
  width: 136px;
  height: 164px;
  border-radius: 20px;
  border: 2px solid var(--primary-primary-800-f-24949, #F24949);
  box-shadow: 12px 0px 32px 0px rgba(0, 0, 0, 0.25);
  margin-right: 20px;
}
.fullscreenModal {
  display: flex;
  flex-direction: column;
  overflow: hidden;
  width: 100%;
  height: 100%;
  background-color: rgb(255, 255, 255);
  position: fixed;
  top: 0;
  z-index: 100;
  left: 0;
  transition: 0.2s;
  &_bottomContent{
    padding: 26px 32px 26px 24px;
    align-items: center;
    height: 26%;
    width: 101%;
    z-index: 10000000;
    white-space: nowrap;
    position: absolute;
    overflow-x: auto;
    display: flex;
    bottom: 0;
    &::-webkit-scrollbar-thumb {
      background: var(--black-500-a-8-a-8-a-8, #A8A8A8); /* Змініть на потрібний колір */
      border-radius: 50px; /* Закруглення кутів ползунка */
    }

    /* Змінюємо колір фону ползунка */
    &::-webkit-scrollbar-track {
      background: var(--black-300-e-5-e-5-e-5, #E5E5E5);
    }

    /* Змінюємо ширину ползунка (не підтримується всіма браузерами) */
    &::-webkit-scrollbar {
      height: 5px; /* Змініть на потрібну ширину */
    }

    img{
      width: 136px;
      height: 164px;
      border-radius: 20px;
      //border: 2px solid var(--primary-primary-800-f-24949, #F24949);
      box-shadow: 12px 0px 32px 0px rgba(0, 0, 0, 0.25);
      margin-right: 20px;
      object-fit: cover;
    }
  }
  &_close{
    position: absolute;
    right: 2%;
    top: 2%;
    z-index: 1000000000;
  }
  &_info{
    display: inline-flex;
    position: absolute;
    z-index: 1000000;
    top: 4.5%;
    left: 3%;
    padding: 15px;
    justify-content: center;
    align-items: center;
    gap: 3px;
    border-radius: 15px;
    background: rgba(24, 24, 24, 0.41);
    h2{
      color: #FFF;
      font-size: 36px;
      font-style: normal;
      font-weight: 600;
      line-height: 32px;
    }
    h3{
      color: #FFF;
      font-size: 18px;
      font-style: normal;
      font-weight: 600;
      line-height: 18px;
    }
  }
  &_mainPhoto{
  &_img{
    z-index: 1000000;
    width: 100%;
    height: 76%;
    object-fit: cover;
    object-position: center;
    position: absolute;
    top: 0;
    left: 0;
  }
  }
}


</style>