<script>
import CustomSelect from "@/components/customSelect.vue";
import LoginModal from "@/components/loginModal.vue";
export default {
  components: {LoginModal, CustomSelect},
  data() {
    return {
      selected: 'Fr', // Початково вибрано "Fr"
      selectedTag: 'Fr', // Назва яка над кастом селект
      options: [{name: "French", tag: "Fr"}, {name: "English", tag: "En"}],
      classCustomSelect: 'custom-select',
      checkedTopOrBottom: true,
      burgerMenu: false,
      searchMenu: false,
      activeCityIndex: -1,
      selectedCity: [[], [], [], [], [], [], [], [], [], [], [], [], [], [], [], [], [], [], [], [], [], [], [], [], [], [], [], [], [], [], [], [], [], [], [], [], [], [], [], [], [], [], [], [], [], [], [],],
      cities: [
        ["Aix-En-Provence (35)", "Annecy (8)", "Avignon (12)", "Bastia (8)", "Bordeaux (26)", "Cannes (24)"],
        ["Dijon (6)", "Grenoble (11)", "Lille (18)", "Lyon (40)", "Marseille (30)", "Monaco (24)"],
        ["Montpellier (9)", "Nancy (7)", "Nantes (25)", "Nice (28)", "Orléans (7)", "Paris (41)"],
        ["Reims (12)", "Rennes (7)", "Strasbourg (28)", "Toulon (5)", "Toulouse (34)", "Tours (7)"],
        ["Adge (2)", "Ajaccio (8)", "Amiens (12)", "Angers (8)", "Belfort (26)", "Besancon (24)"],
        ["Biarritz (6)", "Chambery (11)", "Charleville-Mezieres (18)", "Colmar (40)", "Geneve (30)", "LeMans (24)"],
        ["Adge(2)", "Ajaccio(8)", "Amiens(12)", "Angers(8)", "Belfort(26)", "Besancon(24)"],
        ["Biarritz(6)", "Chambery(11)", "Charleville-Mezieres(18)", "Colmar(40)", "Geneve(30)", "LeMans(24)"]
      ],
      setContentFalse: this.showContent,
      setFilterFalse: this.showFilters,
      selectedCategories: [[], [], [], []],
      columns: [
        ["Private Apt.", "Hotel"],
        ["Blond", "Châtain clair", "Châtain foncé", "Noir", "Roux"],
        ["Very Short", "Short", "Medium", "Long", "Very Long"],
        ["A", "B", "C", "D", "DD"],
        ["18-20", "21-24", "25-29", "30-35", "36-40"],
        ["Anglais", "Allemand", "Italien", "Russe"],
        ["Asiatique", "Métisse", "Caucasien"]],
      columnHeaders: [
        "Disponible",
        "Cheveux",
        "Hair Length",
        "Bonnet",
        "Âge",
        "Langues",
        "Ethnique"
      ],
      selectedFilters: [[], [], [], [], [], [], []],
      autres: [
        ["Taille", "Poids", "Couleur des yeux", "Nationalité", "100% verified"]
      ],
      selectedAutres: [[], [], [], [], []],
      filters: [
          [], [], [], [], [], [], []
      ],
      modalTitle: "Log in",
      modalText: "Login",
      modalPlaceholder: "Type your email",
      isInput: true,
      modalText1: "password",
      modalPlaceholder1: "Type your password",
      isInput1: true,
      resetTitle: 'Récupération de mot de passe',
      resetText: 'Enter email',
      isActiveNav: 1,

    }
  },
  methods: {
    toggleNav(index) {
      this.isActiveNav = index
    },
    filterOpen(index) {
      if (this.filters[index] !== index) {
        this.filters[index] = index
      } else {
        this.filters[index] = null
      }
    },
    toggleCheckbox(index) {
        this.selectedCategories[index] = !this.selectedCategories[index];
      },
      handleBurger() {
        this.burgerMenu = !this.burgerMenu
        const rootElement = document.documentElement;

        if (this.burgerMenu) {
          rootElement.classList.add("no-scroll");
        } else {
          rootElement.classList.remove("no-scroll");
        }
      },
    handlerSearch() {
      this.searchMenu = !this.searchMenu
      const rootElement = document.documentElement;

      if (this.searchMenu) {
        rootElement.classList.add("no-scroll");
      } else {
        rootElement.classList.remove("no-scroll");
      }
    },
      handleMenu() {
        if (this.burgerMenu) {
          this.handleBurger()
        } else if (this.showContent) {
          this.handleContentOff()
        } else {
          this.handleFilterOff()
        }
      },
      handleContentOff() {
        this.$store.state.showContent = false
        const rootElement = document.documentElement;
        rootElement.classList.remove("no-scroll");
      },
      handleFilterOff() {
        this.$store.state.showFilters = false
        const rootElement = document.documentElement;
        rootElement.classList.remove("no-scroll");
      },
      modalHandler(){
        this.$store.state.ifModal = !this.$store.state.ifModal
        const rootElement = document.documentElement;
        rootElement.classList.add("no-scroll");
        console.log(this.$store.state.ifModal)
      },
    },
  computed: {
    showContent() {
      return this.$store.state.showContent;
    },
    showFilters() {
      return this.$store.state.showFilters;
    },
    modal() {
      return this.$store.state.ifModal;
    },
    reset() {
      return this.$store.state.ifReset;
    }
  }
}
</script>

