<template>
  <div>
    <button @click="handleButtonClick(category)"
            :class="{'activeBtn': activeButton === category}" class='greeenFastCategory'>{{ category }}</button>
  </div>
</template>

<script>
export default {
  props: {
    category: String, // Значення категорії
    activeButton: String, // Активна категорія
  },
  methods: {
    handleButtonClick(category) {
      // Викликаємо обробник події і передаємо значення категорії
      this.$emit('button-click', category);
    },
  },
};

</script>

<style lang="scss">

.greeenFastCategory{
  display: flex;
  padding: 10px;
  justify-content: center;
  align-items: center;
  gap: 10px;
  border-radius: 11px;
  background: var(--success-success-80052-a-436, #52A436);
  transition: .2s all;


  color: #FFF;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 14px; /* 100% */
}

</style>