  display: flex;
  padding: 10px 22px;
  justify-content: center;
  align-items: center;
  gap: 10px;
  color: #FFFFFF;
}
.white {
  border-radius: 30px;
  border: 1px solid var(--primary-primary-800-f-24949, #F24949);
}
.modal {
  &__content {
    height: 228px;
  }
  &_btns {
    display: flex;
    flex-direction: row;
    justify-content: center;
    gap: 20px;
  }
  &_close {
    display: flex;
    justify-content: end;
    position: relative;
    right: 0;
    width: 100%;
    align-items: center;
    svg {
      display: flex;
      align-items: center;
      justify-content: center;
      position: relative;
      right: 0;
      //justify-content: end;
    }
  }
  &_title {
    color: var(--black-black-900181818, #181818);
    text-align: center;

    font-size: 24px;
    font-style: normal;
    font-weight: 600;
    line-height: 24px; /* 100% */
    padding-top: 40px;
  }
}
  .favoris {
    &_list {
      display: none;
    }
    &_btn {
      padding-top: 2px;
    }
    &_block {
      display: flex;
      align-items: center;
    }
    &_box {
      margin-top: 30px;
      svg {
        display: flex;
        align-items: center;
        justify-content: center;
      }
      //display: flex;
      display: inline-flex;
      height: 27px;
      padding: 0 22px;
      justify-content: center;
      align-items: center;
      gap: 6px;
      flex-shrink: 0;
      border-radius: 30px;
      border: 1px solid var(--primary-primary-800-f-24949, #F24949);
      background: #FFF;
      color: var(--primary-primary-800-f-24949, #F24949);
      font-family: Josefin Sans;
      font-size: 14px;
      font-style: normal;
      font-weight: 600;
      line-height: 14px; /* 100% */
    }
    &_svg {
      position: absolute;
      right: -10px;
      top: 10px;
      cursor: pointer;
      &_second {
        top: 0;
      }
    }
  }

  @media (max-width: 1050px) {
    .favoris {
       &_list {
         display: flex;
       }
      &_category {
        display: none;
      }
    }
  }
</style>