      return sorted.slice(0, 12);
    },
    premiumCards() {
      const sorted = this.cards.filter(card => card.premium);
      return sorted.slice(0, 12);
    },
    displayedPages() {
      const pages = [];
      const numPagesToShow = this.maxDisplayedPages;

      if (this.totalPage <= numPagesToShow) {
        for (let i = 1; i <= this.totalPage; i++) {
          pages.push(i);
        }
      } else {
        if (this.activePage <= numPagesToShow - 3) {
          for (let i = 1; i <= numPagesToShow - 2; i++) {
            pages.push(i);
          }
          pages.push('...');
          pages.push(this.totalPage);
        } else if (this.activePage >= this.totalPage - numPagesToShow + 3) {
          pages.push(1);
          pages.push('...');
          for (let i = this.totalPage - numPagesToShow + 3; i <= this.totalPage; i++) {
            pages.push(i);
          }
        } else {
          pages.push(1);
          pages.push('...');
          for (let i = this.activePage - 1; i <= this.activePage + 1; i++) {
            pages.push(i);
          }
          pages.push('...');
          pages.push(this.totalPage);
        }
      }
      return pages;
    },
  },
  methods: {
    toggleNav(index) {
      this.isActiveNav = index
    },
    goToPage(page) {
      if (page === 'prev' && this.activePage > 1) {
        this.activePage--;
      } else if (page === 'next' && this.activePage < this.totalPage) {
        this.activePage++;
      } else if (typeof page === 'number') {
        this.activePage = page;
      }
    },
  },
}
</script>

