<template>
  <div class="containerText">
    <div
        class="text-container"
        :class="{ 'expanded': isExpanded }"
        ref="textContainer"
    >
      {{ text }}
    </div>
    <button style="background: none;" @click="toggleExpand">
      <svg xmlns="http://www.w3.org/2000/svg" width="15" height="15" viewBox="0 0 15 15" fill="none" :class="{ 'rotate-icon': isExpanded }">
        <path d="M12.3699 5.55127L8.56655 9.3546C8.11738 9.80377 7.38238 9.80377 6.93322 9.3546L3.12988 5.55127" stroke="#181818" stroke-width="1.5" stroke-miterlimit="10" stroke-linecap="round" stroke-linejoin="round"/>
      </svg>
    </button>
  </div>
</template>

<script>
export default {
  props: {
    text: String,
  },
  data() {
    return {
      isExpanded: false,
    };
  },
  methods: {
    toggleExpand() {
      this.isExpanded = !this.isExpanded;
    },
  },
};
</script>

<style>
.containerText {
  transition: .2s all;
  display: flex;
  flex-direction: column;
  gap: 5px;
}
.text-container {
  transition: .2s all;
  overflow: hidden;
  max-height: 50px; /* Початкова максимальна висота */
}

.text-container.expanded {
  max-height: none; /* Розкриваємо текст */
}
.rotate-icon {
  transition: .2s all;
  transform: rotate(180deg); /* Обертання на 180 градусів */
}
</style>
